/* ===== CSS VARIABLES ===== */
:root {
  /* Responsive breakpoints */
  --bp-xs: 480px;
  --bp-sm: 768px;
  --bp-md: 1024px;
  --bp-lg: 1400px;
  --content-max-width: 1200px;

  /* Accessible color tokens (fallbacks kept) */
  --gold-contrast: #7a5a1a; /* used for outlines and focus */

  --primary-dark: #1a1a1a;
  --charcoal: #2d2d2d;
  --gold: #d4af37;
  --gold-light: #f4e7a6;
  --copper: #b87333;
  --ivory: #fffff0;
  --white: #ffffff;
  --light-gray: #f8f9fa;
  --medium-gray: #e9ecef;
  --text-gray: #6c757d;
  --dark-gray: #495057;
  --shadow: 0 4px 20px rgba(0,0,0,0.08);
  --shadow-hover: 0 8px 32px rgba(0,0,0,0.12);
  --shadow-strong: 0 12px 48px rgba(0,0,0,0.15);
  --border-radius: 12px;
  --border-radius-sm: 6px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-fast: all 0.2s ease;
  --font-heading: 'Playfair Display', serif;
  --font-body: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  --max-width: 1400px;
  --container-padding: 2rem;
}

/* ===== RESET & BASE STYLES ===== */
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

/* Skip link for keyboard users */
.skip-link {
  position: absolute;
  left: -9999px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.skip-link:focus,
.skip-link:active {
  left: 1rem;
  top: 1rem;
  width: auto;
  height: auto;
  padding: 0.5rem 1rem;
  background: var(--gold);
  color: var(--primary-dark);
  z-index: 9999;
  border-radius: 6px;
}

body {
  font-family: var(--font-body);
  line-height: 1.6;
  color: var(--charcoal);
  background: var(--white);
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Screen-reader only utility */
.sr-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

/* ===== TYPOGRAPHY ===== */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-heading);
  font-weight: 500;
  line-height: 1.2;
  color: var(--primary-dark);
}

h1 { font-size: clamp(2.5rem, 4vw, 4rem); }
h2 { font-size: clamp(2rem, 3vw, 3rem); }
h3 { font-size: clamp(1.5rem, 2.5vw, 2.2rem); }
h4 { font-size: clamp(1.25rem, 2vw, 1.5rem); }

p, li { color: var(--text-gray); }

/* ===== UTILITY CLASSES ===== */
.container {
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 0 var(--container-padding);
}

.fade-in {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

/* ===== BUTTONS ===== */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 14px 28px;
  background: linear-gradient(135deg, var(--gold), var(--copper));
  color: var(--primary-dark);
  text-decoration: none;
  border: none;
  border-radius: 50px;
  font-family: var(--font-body);
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

/* Improved focus styles for keyboard navigation */
.btn:focus-visible,
.nav-links a:focus-visible,
.action-btn:focus-visible,
.mobile-menu:focus-visible,
.nav-icon:focus-visible {
  outline: 3px solid rgba(212,175,55,0.18);
  box-shadow: 0 0 0 4px rgba(212,175,55,0.08);
  border-radius: 8px;
}

/* Ensure visible focus for users who tab through links */
:focus:not(:focus-visible) {
  outline: none;
}

.btn:hover::before {
  left: 100%;
}

.btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-hover);
}

.btn-secondary {
  background: transparent;
  border: 2px solid var(--gold);
  color: var(--gold);
}

.btn-secondary:hover {
  background: var(--gold);
  color: var(--primary-dark);
}

/* ===== HEADER & NAVIGATION ===== */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  z-index: 1000;
  transition: var(--transition);
  border-bottom: 1px solid rgba(0,0,0,0.05);
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
  min-height: 80px;
}

/* Ensure page content isn't hidden behind the fixed header */
main#main-content,
.App > main {
  padding-top: 100px; /* matches header min-height + breathing room */
}

.logo {
  font-family: var(--font-heading);
  font-size: 2.2rem;
  font-weight: 700;
  color: var(--primary-dark);
  text-decoration: none;
  background: linear-gradient(135deg, var(--gold), var(--copper));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  transition: var(--transition-fast);
}

.logo:hover {
  transform: scale(1.05);
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 2.5rem;
  align-items: center;
}

.nav-links a {
  color: var(--primary-dark);
  text-decoration: none;
  font-weight: 500;
  position: relative;
  transition: var(--transition-fast);
  padding: 0.5rem 0;
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(135deg, var(--gold), var(--copper));
  transition: width 0.3s ease;
}

.nav-links a:hover {
  color: var(--gold);
}

.nav-links a:hover::after {
  width: 100%;
}

.nav-actions {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.nav-icon {
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: var(--light-gray);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--charcoal);
  font-size: 1.1rem;
  cursor: pointer;
  transition: var(--transition-fast);
  border: none;
}

.nav-icon:hover {
  background: var(--gold);
  color: var(--primary-dark);
  transform: scale(1.1);
}

/* ===== HEADER SEARCH ===== */
.header-search {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  position: relative;
}

.header-search-input {
  width: 0;
  opacity: 0;
  padding: 8px 12px;
  border-radius: 999px;
  border: 2px solid transparent;
  transition: width 280ms ease, opacity 200ms ease, padding 200ms ease;
  font-size: 0.95rem;
  background: var(--white);
  box-shadow: 0 4px 18px rgba(0,0,0,0.06);
}

.header-search.open .header-search-input {
  width: 260px;
  opacity: 1;
  padding: 8px 14px;
  border-color: var(--medium-gray);
}

/* Make header-search responsive */
@media (max-width: 768px) {
  .header-search.open .header-search-input {
    width: 160px;
  }
}

.cart-count {
  position: absolute;
  top: -5px;
  right: -5px;
  background: var(--gold);
  color: var(--primary-dark);
  font-size: 0.75rem;
  font-weight: 700;
  padding: 2px 6px;
  border-radius: 12px;
  min-width: 20px;
  text-align: center;
  line-height: 1.2;
}

.mobile-menu {
  display: none;
  flex-direction: column;
  gap: 3px;
  cursor: pointer;
  padding: 8px;
  background: none;
  border: none;
}

.mobile-menu span {
  width: 25px;
  height: 3px;
  background: var(--charcoal);
  transition: var(--transition-fast);
  border-radius: 2px;
}

/* ===== HERO SECTION ===== */
.hero {
  min-height: 100vh;
  background: linear-gradient(135deg, 
      rgba(26, 26, 26, 0.8), 
      rgba(45, 45, 45, 0.6)), 
      url('https://images.unsplash.com/photo-1586023492125-27b2c045efd7?ixlib=rb-4.0.3&auto=format&fit=crop&w=2000&q=80');
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  color: var(--ivory);
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 30% 70%, transparent 40%, rgba(26, 26, 26, 0.7));
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.hero h1 {
  color: var(--ivory);
  margin-bottom: 1.5rem;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.3);
}

.hero-subtitle {
  font-size: 1.3rem;
  margin-bottom: 2.5rem;
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* ===== SECTIONS ===== */
.section {
  padding: 6rem 0;
  position: relative;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-title {
  margin-bottom: 1rem;
}

.section-subtitle {
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto;
  color: var(--text-gray);
}

/* ===== COLLECTIONS GRID ===== */
.collections-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

/* ===== COLLECTIONS GRID (continued) ===== */
.collection-card {
  position: relative;
  height: 400px;
  border-radius: var(--border-radius);
  overflow: hidden;
  cursor: pointer;
  transition: var(--transition);
  box-shadow: var(--shadow);
}

.collection-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(26, 26, 26, 0.7), transparent 60%);
  z-index: 1;
  transition: var(--transition);
}

.collection-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-hover);
}

.collection-card:hover::before {
  background: linear-gradient(45deg, rgba(26, 26, 26, 0.5), transparent 70%);
}

.collection-bg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.collection-card:hover .collection-bg {
  transform: scale(1.05);
}

.collection-content {
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  right: 2rem;
  color: var(--ivory);
  z-index: 2;
}

.collection-content h3 {
  color: var(--ivory);
  margin-bottom: 0.5rem;
  font-size: 1.8rem;
}

.collection-content p {
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1.5rem;
}

/* ===== PRODUCTS SECTION ===== */
.products-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
  margin-bottom: 3rem;
}

.search-filters {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.search-box {
  position: relative;
}

.search-input {
  width: 300px;
  padding: 12px 20px 12px 45px;
  border: 2px solid var(--medium-gray);
  border-radius: 25px;
  font-size: 1rem;
  transition: var(--transition-fast);
  background: var(--white);
}

.search-input:focus {
  outline: none;
  border-color: var(--gold);
  box-shadow: 0 0 0 3px rgba(212, 175, 55, 0.1);
}

.search-icon {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--text-gray);
  pointer-events: none;
}

.filter-select {
  padding: 12px 16px;
  border: 2px solid var(--medium-gray);
  border-radius: 8px;
  background: var(--white);
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition-fast);
}

.filter-select:focus {
  outline: none;
  border-color: var(--gold);
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

/* Make image-heavy grids a bit more resilient on very wide screens */
.products-grid {
  max-width: var(--content-max-width);
  margin: 0 auto;
}

.product-card {
  background: var(--white);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  transition: var(--transition);
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-hover);
}

.product-image {
  position: relative;
  height: 280px;
  overflow: hidden;
  background: var(--light-gray);
}

.product-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.product-card:hover .product-img {
  transform: scale(1.05);
}

.product-actions {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  opacity: 0;
  transition: var(--transition);
}

.product-card:hover .product-actions {
  opacity: 1;
}

.action-btn {
  width: 44px;
  height: 44px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: var(--transition-fast);
  color: var(--charcoal);
  font-size: 1rem;
}

.action-btn:hover {
  background: var(--gold);
  color: var(--primary-dark);
  transform: scale(1.1);
}

.action-btn.active {
  background: var(--gold);
  color: var(--primary-dark);
}

.product-info {
  padding: 1.5rem;
}

.product-category {
  font-size: 0.85rem;
  color: var(--text-gray);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 0.5rem;
}

.product-name {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--primary-dark);
  margin-bottom: 0.5rem;
  font-family: var(--font-heading);
}

.product-price {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--gold);
  margin-bottom: 1rem;
}

.product-rating {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.stars {
  color: var(--gold);
  font-size: 1rem;
}

.rating-count {
  color: var(--text-gray);
  font-size: 0.9rem;
}

/* ===== REVIEWS SECTION ===== */
.reviews-section {
  background: var(--light-gray);
}

.reviews-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.review-card {
  background: var(--white);
  padding: 2rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  transition: var(--transition);
}

.review-card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-hover);
}

.review-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.reviewer-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--gold);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-dark);
  font-weight: 600;
  font-size: 1.2rem;
}

.reviewer-info h4 {
  margin: 0;
  font-size: 1.1rem;
}

.review-rating {
  color: var(--gold);
  margin-bottom: 1rem;
}

.review-text {
  line-height: 1.6;
  color: var(--text-gray);
}

/* ===== CART SIDEBAR ===== */
.cart-sidebar {
  position: fixed;
  top: 0;
  right: -450px;
  width: 450px;
  height: 100vh;
  background: var(--white);
  box-shadow: -10px 0 30px rgba(0, 0, 0, 0.1);
  z-index: 1500;
  transition: right 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  flex-direction: column;
}

.cart-sidebar.open {
  right: 0;
}

.cart-header {
  padding: 2rem 1.5rem 1rem;
  border-bottom: 2px solid var(--light-gray);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-header h3 {
  margin: 0;
  font-size: 1.5rem;
}

.cart-items {
  flex: 1;
  padding: 1.5rem;
  overflow-y: auto;
}

.cart-empty {
  text-align: center;
  padding: 4rem 2rem;
  color: var(--text-gray);
}

.cart-empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.cart-footer {
  padding: 1.5rem;
  border-top: 2px solid var(--light-gray);
  background: var(--light-gray);
}

.cart-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  font-size: 1.3rem;
  font-weight: 600;
}

.cart-total-price {
  color: var(--gold);
}

/* ===== MODAL ===== */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: 2rem;
}

.modal.active {
  display: flex;
}

.modal-content {
  background: var(--white);
  border-radius: var(--border-radius);
  max-width: 500px;
  width: 100%;
  padding: 2rem;
  position: relative;
  animation: modalSlideIn 0.3s ease-out;
  max-height: 90vh;
  overflow-y: auto;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(50px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 44px;
  height: 44px;
  background: rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 1.2rem;
  color: var(--charcoal);
  transition: var(--transition-fast);
}

.modal-close:hover {
  background: var(--gold);
  color: var(--primary-dark);
}

/* ===== FOOTER ===== */
.footer {
  background: var(--primary-dark);
  color: var(--ivory);
  padding: 4rem 0 2rem;
}

/* Reduced-motion preference */
@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
  }
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 3rem;
  margin-bottom: 3rem;
}

.footer-section h4 {
  color: var(--gold);
  margin-bottom: 1.5rem;
  font-family: var(--font-heading);
}

.footer-section p {
  color: rgba(255, 255, 255, 0.8);
}

.footer-links {
  list-style: none;
}

.footer-links li {
  margin-bottom: 0.8rem;
}

.footer-links a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  transition: var(--transition-fast);
}

.footer-links a:hover {
  color: var(--gold);
}

.social-icons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.social-icon {
  width: 44px;
  height: 44px;
  background: var(--charcoal);
  color: var(--ivory);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  text-decoration: none;
  transition: var(--transition);
  font-size: 1.1rem;
}

.social-icon:hover {
  background: var(--gold);
  color: var(--primary-dark);
  transform: translateY(-2px);
}

.newsletter-form {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.newsletter-input {
  flex: 1;
  padding: 12px 16px;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
}

.footer-bottom {
  border-top: 1px solid var(--charcoal);
  padding-top: 2rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
}

/* ===== NOTIFICATIONS ===== */
.notification {
  position: fixed;
  top: 100px;
  right: 20px;
  background: var(--gold);
  color: var(--primary-dark);
  padding: 1rem 1.5rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-strong);
  z-index: 2100;
  animation: slideInRight 0.3s ease-out;
  max-width: 350px;
  font-weight: 500;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.notification.success {
  background: #28a745;
  color: white;
}

.notification.error {
  background: #dc3545;
  color: white;
}

.notification.info {
  background: #17a2b8;
  color: white;
}

/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 1024px) {
  :root {
    --container-padding: 1.5rem;
  }
  
  .search-input {
    width: 250px;
  }
  
  .cart-sidebar {
    width: 400px;
    right: -400px;
  }
  
  .cart-sidebar.open {
    right: 0;
  }
}

@media (max-width: 768px) {
  :root {
    --container-padding: 1rem;
  }
  
  .nav-links {
    display: none;
  }
  
  .mobile-menu {
    display: flex;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
  }
  
  .collections-grid {
    grid-template-columns: 1fr;
  }
  
  .products-header {
    flex-direction: column;
    align-items: stretch;
  }
  
  .search-filters {
    justify-content: center;
  }
  
  .search-input {
    width: 100%;
  }
  
  .cart-sidebar {
    width: 100vw;
    right: -100vw;
  }
  
  .cart-sidebar.open {
    right: 0;
  }
  
  .modal {
    padding: 1rem;
  }

  /* Mobile navigation open state (toggled by adding .mobile-open to .nav-links) */
  .nav-links.mobile-open {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 72px;
    left: 0;
    right: 0;
    background: rgba(255,255,255,0.98);
    padding: 1rem;
    gap: 1rem;
    box-shadow: 0 8px 24px rgba(0,0,0,0.08);
    z-index: 1050;
  }

  .mobile-menu.open span:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }
  .mobile-menu.open span:nth-child(2) {
    opacity: 0;
  }
  .mobile-menu.open span:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }
}

@media (max-width: 480px) {
  .nav {
    padding: 0.5rem 0;
  }
  
  .logo {
    font-size: 1.8rem;
  }
  
  .nav-actions {
    gap: 1rem;
  }
  
  .nav-icon {
    width: 40px;
    height: 40px;
  }
  
  .hero-content {
    padding: 0 1rem;
  }
  
  .section {
    padding: 4rem 0;
  }
  
  .products-grid {
    grid-template-columns: 1fr;
  }
  
  .reviews-grid {
    grid-template-columns: 1fr;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
  }
  
  /* On very small screens, make product actions inline and visible */
  .product-actions {
    position: relative;
    top: auto;
    right: auto;
    flex-direction: row;
    justify-content: flex-end;
    gap: 0.5rem;
    opacity: 1;
    padding: 0.5rem 1rem;
    background: transparent;
  }
}

/* ===== REVIEW MODAL (simple) ===== */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2200;
}
.modal-backdrop .modal {
  background: var(--white);
  border-radius: 12px;
  padding: 1.25rem;
  width: 100%;
  max-width: 540px;
  box-shadow: var(--shadow-strong);
}
.modal-backdrop input,
.modal-backdrop textarea {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid var(--medium-gray);
  margin-top: 0.5rem;
}
.product-reviews .review-item {
  padding: 0.5rem 0;
  border-top: 1px dashed var(--medium-gray);
  margin-top: 0.5rem;
}